import React, { useEffect, useState } from "react";
import DateContext from "./DateContext";

const DateState = ({ children }) => {
  const [fecha, setFecha] = useState("aaaa-mm-dd");

  const handleChangeFecha = (e) => {
    setFecha(e.target.value);
  };

  useEffect(() => {
    const hoy = new Date().toLocaleString('es-ES', {year: 'numeric', month: '2-digit', day: '2-digit'});
    const hoyParsed = hoy.split("/").reverse().join("-")
    setFecha(hoyParsed);
  }, [])

  return (
    <DateContext.Provider
      value={{
        fecha,
        handleChangeFecha,
      }}
    >
      {children}
    </DateContext.Provider>
  );
};

export default DateState;
